//------------Any Questions----------//

.contact_and_form {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 60px;
  padding-top: 80px;


  &__container {
    padding: 0 15px;

    @include tablet {
      max-width: 900px;
      display: flex;
    }
  }
}

.questions_container {
  @include tablet {
    flex: 50%;
  }
  h2 {
    font-size: 3.0625rem;
    line-height: 3.5625rem;
    font-family: $font-secondary;
  }

  p {
    margin: 5px 0 35px 0;
  }
}


.contact_container {

  @include tablet {
    display: flex;
  }

  span {
    font-weight: 600;
    font-size: 1rem;
    line-height: 1.375rem;
    color: $text-color;
    padding-right: 10px;

    display: flex;
    align-items: center;

    &::before {
      content: "";
      height: 30px;
      width: 30px;
      display: inline-block;
      background-repeat: no-repeat;
      margin-top: 5px; //przesunicie ikonek, eby byy na rodku napisu
    }


    &.email::before {
      background-image: url("/Desktop/Mail Icon.svg");

    }

    &.phone::before {
      background-image: url("/Desktop/Phone Icon.svg");
    }
  }
}

//-----------------FORM-----------------//

.form_container {
  margin-top: 20px;

  @include tablet {
    flex: 50%;
  }
}

.contact__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .label {

    font-weight: bold;
    font-size: 0.6875rem;
    line-height: 0.9375rem;
    letter-spacing: 0.205rem;
    color: $primary-color;
    padding-top: 20px;

    &:first-child {
      padding-top: 0;
    }
  }

  .input {
    border: none;
    border-bottom: 1px solid #95989A;
    height: 20px;
    width: 100%;
    padding: 20px;

  }
}

/*----------checkbox------------*/

.checkbox_label {
  flex-shrink: 0;
  flex-basis: 60px;
  cursor: pointer;
  display: flex;
  position: relative;
  margin: 20px 15px 20px 0;
  padding-left: 35px;
  font: normal normal normal 12px/17px;
  color: #707070;

  /* Hide the browser's default checkbox */
  .checkbox_input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    /*!* When the checkbox is checked, add a background *!*/
    &:checked ~ .checkmark {
      background-image: url("/Desktop/Checkmark.svg");
      background-size: auto;
      background-position: center;
      background-repeat: no-repeat;
    }
}
  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 22px;
    width: 22px;
    background-color: #FFF;

    padding: 5px;

    border: 1px solid #95989A;
    border-radius: 4px;
  }
}




/*------------checkbox end----------*/